<template>
    <div>
        <div class="panel-nav">
            <el-breadcrumb separator="/" class="m-l-lg">
                <el-breadcrumb-item>
                    首页
                </el-breadcrumb-item>
                <el-breadcrumb-item>
                    nihao
                </el-breadcrumb-item>
            </el-breadcrumb>
            <el-button class="btn btn-gradient m-r-lg" @click="showDialog">
                {{$t('releaseHomework')}}
            </el-button>
        </div>
        <div class="panel-body" v-if="mode === 'grid'">
            <el-select v-model="search.select" @change="change">
                <el-option v-for="item in keyList"
                    :key="item.id"
                    :label="item.name"
                    :value="item.value">
                </el-option>
            </el-select>
            <el-input v-model="search.text" suffixIcon="el-icon-search" @input="searchHomework"
                      class="input-search"></el-input>
            <homework-grid v-for="item in list" :key="item.index" class="m-t" :header="item[0]"
                           :data="item[1]" @showInfo="showInfo"></homework-grid>
        </div>
        <div class="panel-body-list clearfix" v-if="mode === 'list'">
            <list-table
                :data="detailInfo.listData"
                :fields="detailInfo.fields"
                :operators="detailInfo.operators"
                class="wrapper-md"
                :operators-title="$t('operate.title')">
                <div slot="btns-bottom" slot-scope="slotData">
                    <el-button class="btn btn-manage" @click="removeItems(slotData)">{{$t('operate.remove')}}</el-button>
                </div>
            </list-table>
        </div>
        <el-dialog :visible.sync="dialogVisable" :title="$t('releaseHomework')" custom-class="dialog-lg" top="0">
            <homework-create></homework-create>
        </el-dialog>

    </div>
</template>
<script type="text/javascript">
    import listTable from '../table/table'
    import homeworkCreate from './homeworkCreate'
    import homeworkGrid from './homeworkGrid'
    import _ from 'lodash'

    export default {
        created () {
            this.getList()
        },
        computed: {
            list() {
                let list
                if (!this.grid.list.length) {
                    return
                }
                list = _.groupBy(this.grid.list, (item) => {
                    return item[this.search.select]
                })
                return Object.entries(list).sort((a, b) => {
                    if (this.search.select === 'time') {
                        return a[0] > b[0] ? -1 : 1
                    }
                    return a[0] > b[0] ? 1 : -1
                })
            }
        },
        data () {
            return {
                mode: 'grid',
                fileList: [],
                dialogVisable: false,
                search: {
                    select: 'time',
                    text: ''
                },
                keyList: [{
                    id: 1,
                    name: '按时间',
                    value: 'time'
                }, {
                    id: 2,
                    name: '按班级',
                    value: 'className'
                }, {
                    id: 3,
                    name: '按评分',
                    value: 'score'
                }],
                selectedKey: '',
                grid: {
                    header: '',
                    list: []
                },
                detailInfo: {
                    listData: [],
                    fields: [{
                        name: 'name',
                        showName: this.$t('homework.name')
                    }, {
                        name: 'status',
                        showName: this.$t('homework.status')
                    }, {
                        name: 'score',
                        showName: this.$t('homework.grade'),
                        formatToVnode: this.showStar
                    }],
                    operators: [{
                        name: this.$t('operate.edit'),
                        handler: this.edit
                    }, {
                        name: this.$t('operate.remove'),
                        handler: this.remove
                    }],
                }
            }
        },
        methods: {
            change (data) {
                this.selectedKey = data
                this.search.text = ''
            },
            getList () {
                this.$http.get('/homework/list').then((res) => {
                    this.homeworkList = res.data
                    this.grid.list = res.data
                })
            },
            showDialog () {
                this.dialogVisable = true
            },
            searchHomework (val) {
                if (!val) {
                    this.grid.list = this.homeworkList
                    return
                }
                this.grid.list = this.homeworkList.filter((item) => {
                    return ~(item[this.search.select] + '').indexOf(val.trim())
                })
            },
            showInfo(data) {
                console.log(data.id)
                this.mode = 'list'
                //todo 查询
            },
            removeItems() {

            },
            showStar() {
                let items = []
                const isOdd = value % 2 === 1
                const onNum = parseInt(value / 2)
                const offNum = isOdd ? 4 - onNum : 5 - onNum
                for (let i = 0; i < onNum; i++) {
                    items.push(<i class="el-rate__icon el-icon-star-on rate-on"></i>)
                }
                if (isOdd) {
                    items.push(<i class="el-rate__icon el-icon-star-on"><i class="el-rate__decimal el-icon-star-on rate-half"></i></i>)
                }
                for (let i = 0; i < offNum; i++) {
                    items.push(<i class="el-rate__icon el-icon-star-on"></i>)
                }
                return (<span title={value}>{items}</span>)
            }
        },
        components: {
            homeworkCreate,
            homeworkGrid,
            listTable
        }
    }
</script>
<i18n>
    {
        "zh": {
            "username": "上传",
            "clickToUpload": "点击上传",
            "releaseHomework": "发布作业",
            "operate": {
                "title": "操作",
                "remove": "删除"
            },
            "homework": {
                "name": "学生姓名",
                "status": "状态",
                "grade": "成绩"
            }
        }
    }
</i18n>
<style type="text/css">
    .input-search{
        width: 280px;
        height: 30px;
    }
    .panel-nav {
        width: 100%;
        height: 60px;
        background-color: #ffffff;
        box-shadow: 1px 1px 2px 0px rgba(4, 4, 4, 0.06);
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    .panel-body {
        background-color: #ffffff;
        border: solid 3px #ffffff;
        margin: 25px;
        padding: 40px 30px 40px 40px;
    }

    .dialog-lg {
        min-height: 100%;
        margin: 0;
        float: right;
        width: 1360px;
        /*width: 70%;*/
        /*-webkit-animation: diy 5s;*/
        /*animation: diy 0.2s linear;*/
    }

    .dialog-lg > .el-dialog__header {
        height: 79px;
        background-color: #ffffff;
        box-shadow: 0px 3px 1px 0px rgba(4, 4, 4, 0.06);
        display: flex;
        align-items: center;
        padding: 0;
    }

    .el-dialog__header > span {
        font-family: MicrosoftYaHei;
        font-size: 18px;
        line-height: 0px;
        letter-spacing: 0px;
        color: #696e79;
    }

    .el-upload-list__item {
        width: 200px;
        height: 40px;
        background-color: rgba(69, 154, 222, 0.08);
        border-radius: 1px;
        display: flex;
        align-items: center;
    }

    .el-upload-list__item a {
        font-size: 14px;
        color: #696e79;
        margin-left: 15px;
    }

    .el-upload-list__item .el-icon-close {
        line-height: 30px;
        color: #fa7252;
        font-size: 14px;
        font-weight: bold;
        display: none;
    }
    .el-upload-list__item label {
        line-height: 30px;
        position: absolute;
        top: 5px;
    }

    .el-upload-list__item .el-progress {
        top: 30px;
        left: 0;
    }
    .el-upload-list__item .el-icon-close {
        display: inline-block;
    }

    @keyframes diy {
        from {
            width: 0;
        } to {
            width: 70%;
        }
    }
</style>



