<template>
    <div class="panel-grid">
        <div class="panel-grid-header">
            <span @click="dateFormat">{{header}}</span>
        </div>
        <div class="panel-grid-body clearfix">
            <section v-for="item in data" :key="item.id" class="panel-grid-content pull-left m-r-xl m-b-xl" @click="showInfo(item)">
                <header>
                    <span class="m-l-md">{{item.name}}</span>
                </header>
                <article>
                    <span>{{dateHandler(item.time)}}</span><br/>
                    <span v-text="$t('grid.content.submitted')"></span>
                    <div :class="[item.submited===item.all?'text-success-lt':'','inline']">
                        <span
                            :class="[item.submited===item.all?'text-success-lt':'text-danger-dker']">{{item.submited}}</span>
                        <span>/</span>
                        <span>{{item.all}}</span>
                    </div>
                    <br/>
                    <span v-text="$t('grid.content.corrected')"></span>
                    <div :class="[item.corrected===item.all?'text-success-lt':'','inline']">
                        <span :class="[item.corrected===item.all?'text-success-lt':'text-primary-lt']">{{item.corrected}}</span>
                        <span>/</span>
                        <span>{{item.all}}</span><br>
                    </div>
                    <span v-text="$t('grid.content.evaluate')"></span>
                    <el-rate class="grid-rate m-t"
                             :allow-half="true"
                             v-model="item.score"
                             :disabled="true">
                    </el-rate>
                </article>
                <footer>
                    <span>{{item.className}}</span>
                </footer>
            </section>
        </div>
    </div>
</template>
<script type="text/javascript">
    import {dateFormat} from '@/assets/js/untils'

    export default {
        data() {
            return {}
        },
        props: ['data', 'header'],
        methods: {
            dateHandler(time) {
                return dateFormat(new Date(time).getTime(), 'yyyy/MM/dd')
            },
            showInfo(data) {
                this.$emit('showInfo', data)
            },
            dateFormat
        }
    }
</script>
<i18n>
    {
        "zh": {
            "grid": {
                "content": {
                    "submitted": "已提交：",
                    "corrected": "已批改：",
                    "evaluate": "整体评价："
                }
            }
        }
    }
</i18n>
<style scoped>
    .panel-grid-header {
        font-family: MicrosoftYaHei;
        font-size: 12px;
        line-height: 36px;
        color: #0c92fe;
    }

    .panel-grid-content {
        width: 300px;
        height: 300px;
        background-color: rgba(245, 247, 248, 0.04);
        border: solid 1px rgba(202, 216, 223, 0.4);
        font-size: 0;
        position: relative;
    }

    .panel-grid-content > header {
        height: 48px;
        font-family: MicrosoftYaHei;
        font-size: 18px;
        color: #696e79;
        border-bottom: 1px solid rgba(12, 146, 254, 0.05);
        display: flex;
        align-items: center;
    }

    .panel-grid-content > article {
        line-height: 36px;
        padding: 10px 10px 10px 20px;
        color: #696e79;
    }

    .panel-grid-content > article span {
        font-size: 14px;
        font-family: MicrosoftYaHei;
    }

    .panel-grid-content > footer {
        font-size: 12px;
        color: #696e79;
        text-align: right;
    }

    .panel-grid-content > footer > span {
        display: inline-block;
        background-color: #f5f7f8;
        border-radius: 17px;
        padding: 11px 17px;
        position: absolute;
        right: 10px;
        bottom: 10px;
    }

    .grid-rate {
        display: inline-block;
        line-height: 36px;
        margin-left: 10px;
    }

    .inline {
        display: inline;
    }
</style>



