<template>
    <div class="clearfix">
        <div class="h-c-left">

        </div>
        <div class="h-c-right">
            <div class="panel">
                <div class="panel-header text-left">
                    <span v-text="$t('type')"></span>
                </div>
                <div class="panel-body text-left">
                    <button :class="['btn', isActive?'btn-transform-active':'btn-transform']"
                            @click="changeActive(true)">
                        <span v-text="$t('newHomework')"></span>
                    </button>
                    <button :class="['btn', !isActive?'btn-transform-active':'btn-transform']"
                            @click="changeActive(false)">
                        <span v-text="$t('selectModule')"></span>
                    </button>
                    <div class="panel-homework">
                        <el-table :data="tableData" style="width: 100%">
                            <el-table-column prop="name"></el-table-column>
                            <el-table-column prop="profession"></el-table-column>
                            <el-table-column prop="releaseTime"></el-table-column>
                            <el-table-column>
                                <template slot-scope="scope">
                                    <!--<a @click=""><span></span></a>-->
                                </template>
                            </el-table-column>
                        </el-table>
                    </div>
                </div>
            </div>
            <div class="panel" :disabled="!isDesign">
                <div class="panel-header text-left">
                    <span v-text="$t('design')"></span>
                </div>
                <div class="panel-body text-left">
                    <el-form ref="form" :model="form" label-width="117px">
                        <el-form-item :label="$t('form.name')" required>
                            <el-col :span="14">
                                <el-input :placeholder="$t('form.placeholder.name')" v-model="form.name"
                                          :disabled="!isDesign"></el-input>
                            </el-col>
                        </el-form-item>
                        <el-form-item :label="$t('form.content')" required>
                            <el-col :span="21">
                                <el-input type="textarea" v-model="form.content" :disabled="!isDesign"
                                    :autosize="{minRows: 5}"></el-input>
                            </el-col>
                        </el-form-item>
                        <el-form-item :label="$t('form.submitDate')" required>
                            <el-col :span="14">
                                <el-date-picker
                                    v-model="form.submitDate"></el-date-picker>
                            </el-col>
                        </el-form-item>
                        <!-- /attachments/attachment_upload --><!-- multiple -->
                        <el-form-item :label="$t('form.appendix')">
                            <el-col :span="14">
                                <el-upload action="/homework/attachments/attachment_upload"
                                           :auto-upload="false"
                                           :limit="3"
                                           multiple
                                           :on-success="uploadSuccess"
                                           :on-remove="removeFile"
                                           :file-List="form.fileList"
                                           ref="upload">
                                    <!-- :headers="{'Authorization':'adsfasfasfasdsafdasds'}" -->
                                    <el-button slot="trigger" class="btn" type="primary"
                                               v-text="$t('selectFiles')"></el-button>
                                    <el-button type="success" class="btn" v-text="'clickToUpload'"
                                               @click="uploadFiles()"></el-button>
                                </el-upload>
                            </el-col>
                        </el-form-item>
                        <el-form-item :label="$t('form.saveAs')">
                            <el-col :span="14">
                                <el-switch v-model="form.isSave"></el-switch>
                            </el-col>
                        </el-form-item>
                        <el-form-item>
                            <el-col :span="14">
                                <el-button v-text="$t('form.finish')" class="btn btn-gradient"></el-button>
                            </el-col>
                        </el-form-item>
                    </el-form>
                </div>
            </div>
            <div class="panel clearfix text-left">
                <div class="panel-header">
                    <span v-text="$t('courseGroup')"></span>
                </div>
                <div class="panel-body text-left">
                    <button :class="['btn',courseGroupId === item.id?'btn-transform-active':'btn-transform']"
                            @click="changeCourseGroup(item.id)"
                            v-for="item in courseGroupList"
                            :key="item.id">
                        <span v-text="item.name"></span>
                    </button>
                    <br/>
                    <el-button v-text="$t('form.release')" class="btn btn-gradient"></el-button>
                </div>
            </div>
        </div>
    </div>
</template>
<script type="text/javascript">
    export default {
        created() {
            this.getCourseGroup()
        },
        data() {
            return {
                isActive: true,
                isDesign: true,
                form: {
                    name: '',
                    content: '',
                    submitDate: (() => +new Date())(),
                    fileList: [],
                    isSave: false,
                    fileIds: []
                },
                courseGroupId: 0,
                courseGroupList: [],
                tableData: [{name: 'asd', profession: '1321', releaseTime: 12321312}]
            }
        },
        methods: {
            changeActive(status) {
                this.isActive = status
            },
            uploadFiles() {
                this.$refs.upload.submit()
            },
            changeCourseGroup(id) {
                this.courseGroupId = id
            },
            uploadSuccess(res, file) {
                let opt = {
                    id: res[0],
                    uid: file.uid
                }
                this.form.fileIds.push(opt)
            },
            removeFile(file) {
                this.form.fileIds.some((item) => {
                    if (file.uid === item.uid) {
                        Array.splice(this.form.fileIds, this.form.fileIds.indexOf(item))
                    }
                })
            },
            getCourseGroup() {
                // this.$http({
                //     url: '/homework/classEntity',
                //     methods: 'get'
                // }).then((res) => {
                //     this.courseGroupList = res.data.data;
                // }).catch((err) => {
                //     console.err(err);
                // })
            },
            timeHandler() {
                return '13213'
            }
        }
    }
</script>
<i18n>
    {
        "zh": {
            "type": "方式",
            "design": "作业设计",
            "newHomework": "新建作业",
            "selectModule": "选择模板",
            "selectFiles": "选择文件",
            "courseGroup": "课程组",
            "form": {
                "name": "作业标题",
                "content": "作业内容",
                "submitDate": "截止日期",
                "appendix": "附件",
                "saveAs": "保存为作业模板",
                "finish": "完成",
                "release": "发布",
                "placeholder": {
                    "name": "请输入作业标题",
                    "content": "请输入作业内容"
                }
            }
        }
    }
</i18n>
<style scoped>
    .h-c-left {
        float: left;
        width: 480px;
        height: 600px;
    }

    .h-c-right {
        float: left;
        padding-left: 30px;
    }

    .btn-transform {
        width: 200px;
        height: 70px;
        background-color: #f5f7f8;
        border: solid 1px #cad8df;
        margin-bottom: 20px;
    }

    .btn-transform-active {
        width: 200px;
        height: 70px;
        background-color: #f5f7f8;
        border-style: solid;
        border-width: 1px;
        border-image-source: linear-gradient(0deg, #0090ff 0%, #6c50fd 100%);
        border-image-slice: 1;
        margin-bottom: 20px;
    }

    .h-c-right .panel {
        border-bottom: solid 1px #dcdfe2;
        width: 770px;
        padding-bottom: 30px;
    }

    .h-c-right .panel-header {
        padding: 20px;
    }

    .h-c-right .panel-header > span {
        font-family: MicrosoftYaHei;
        font-size: 18px;
        color: #0c92fe;
    }

    .h-c-right .panel-body {
        margin-left: 55px;
    }

    .h-c-right .panel-body button:not(:nth-child(3n)) {
        margin-right: 20px;
    }

    .h-c-right .el-col {
        padding-left: 17px;
    }

    .panel-homework {
        width: 680px;
        height: 227px;
    }

    .panel-homework-header {
        height: 50px;
        background-color: #f5f7f8;
    }

</style>



